            let body = "<p>ÏÉÅÍ∏∞ Ï†úÎ™© Í¥ÄÎ†® ÌïòÏó¨ ÏïÑÎûòÏôÄ Í∞ôÏù¥ Ï≤®Î∂Ä ÏÑúÎ•ò ÌôïÏù∏ Í≤∞Í≥º Ï†ÑÎã¨ ÎìúÎ¶ΩÎãàÎã§.</p><p>";
            if (this.tabs === 1) {
                this.form.title = "Ïä§ÎßàÌä∏Í≥µÎ∞© Ïã†Ï≤≠ ÏÑúÎ•ò ÌôïÏù∏ ÏïàÎÇ¥";
                body = body + "Í≥µÎ∞© Ïã†Ï≤≠ ÏÑúÎ•ò</p>" + await this.makeBody(this.fileAdds);
            } else if (this.tabs === 2) {
