                body = body + "Í≥µÎ∞© Ï∂îÍ∞Ä ÏÑúÎ•ò</p>" + await this.makeBody(this.fileAddsB);
            }
            this.form.body = body;
            this.$refs.dialog.open();
            // this.$refs.dialog.editor.setContent('<p>This is <strong>some</strong> inserted text. üëã</p>');
            // const data = await this.$axios.get(`/api/shopinfo/getShopDocChkMail?i_shop=${this.itemInput.i_shop}&i_no=${this.itemInput.i_no}&f_gubun=${this.tabs}`);
        },
