            let body = ""
            items.forEach((data) => {
                let n_status = data.f_noact=='Y' ? "Ï†ëÏàò" : data.f_noact=='N' ? "Î∞òÎ†§" : data.f_noact=='I' ? 'Í≤ÄÌÜ†' : data.f_noact=='R' ? 'Í≤ÄÌÜ†' : 'ÎØ∏Îì±Î°ù';
